(function (document, window, $) {

    /**
     * 页面中显示的评定星星的个数
     * @param num
     */
    function showStarsNum(num) {
        var starsBox = $('.start-box-right .stars-box li');
        num = Math.round(num);
        console.log(num);
        if(num < 6){
            for(var i=0; i<num; i++){
                starsBox.get(i).className = 'img-stars img-stars-empty'
            }
        }else {
            console.log('星星数量设置失败')
        }
    }

    window.showStarsNum = showStarsNum;

})(document, window, $);


