(function () {
    var URL_PARAMS;
    $.getParam = function (str) {
        if (!URL_PARAMS) {
            URL_PARAMS = {};
            var params = location.search;
            $.each(params.substring(1).split("&"), function (i, v) {
                var pos = v.indexOf("=");
                var key = v.substring(0, pos);
                var val = v.substring(pos + 1);
                key = decodeURIComponent(key);
                val = decodeURIComponent(val);
                URL_PARAMS[key] = val;
            })
        }

        return URL_PARAMS[str] || '';
    };
})();


