//截图
(function (document, window, $) {

    /**
     * canvas 转 images
     * @param canvas
     * @returns {*}
     */
    function convertCanvasToImage(canvas) {
        var image = new Image();

        console.log(canvas);
        image.setAttribute('crossOrigin', 'anonymous');
        image.src = canvas.toDataURL("image/png");

        return image;
    }

    window.convertCanvasToImage = convertCanvasToImage;


})(document, window, $);


