require('./../comm/getuseragent');

(function (document, window, $) {

    var chartFrom = require('./charts');

    var myMapOption = {
        myMap: '',
        myStartPoint: '',
        myEndPoint: '',
        myRoutes: '',
        myMapCenter: '',
        myPic: {
            picBottom: '',
            picLine: '',
            picText: '',
            picReal: '',
            picStart: '',
            picEnd: ''
        },
        myPageType: 1,
        myWebLg: 'en'
    };

    window.onload = function () {

        var language = $.getParam('language');
        setPagelanguage(language);

        var userAgent = MygetUserAgentFun();

        try {
            console.log(userAgent);
            if (userAgent == 'ios') {
                window.webkit.messageHandlers.PaceHistoryForDm.postMessage({method: 'WebIsReady'});
            } else {
                PaceHistoryForDm.WebIsReady(1);
            }
            console.log("已通知客户端(PaceHistoryForDm)");
        } catch (e) {
            console.log("通知客户端失败(PaceHistoryForDm)");
        }
    };

    function setPagelanguage(language) {

        var time = $('.content-info-time em');
        var kilometre = $('.content-info-kilometre em');
        var pace = $('.content-info-pace em');
        var heartFive = 'heart_five.html?language=' + $.getParam('language');
        if (language === 'zh_cn') {
            time.text('时长');
            kilometre.text('公里');
            pace.text('配速');
            $('.pace-normal-content').eq(0).html("<span>0</span>消耗");
            $('.pace-normal-content').eq(1).html("<span>0</span>平均心率");

            //每公里配速
            var heart_href = $('.chart-box-pace .chart-box-title').attr('href') + '?language=' + $.getParam('language');
            $('.chart-box-pace .chart-box-title').attr('href', heart_href);

            $('.chart-box-pace .chart-box-title').html('每公里配速' + "<span>（分钟/公里）</span>" + "<a href=" + heart_href + "><img src='/images/arrow@2x.png' alt='arrow'></a>");
            $('.chart-box-pace .chart-box-title').click(function (e) {
                e.stopPropagation();
                window.location.href = heart_href;
            });

            //实时配速
            $('.chart-box-real-pace .chart-box-title').html("实时配速<span>（分钟/公里）</span>");

            //心率无区间
            $('.chart-box-heart .chart-box-title').html("心率五区间占比<a href=" + heartFive + "><img src='/images/ic_info_black_normal@2x.png' style='position: relative;width: 0.48rem;height: 0.48rem;vertical-align: middle;top: -0.04rem;left: 0.2rem'></a>")

            //实时心率
            $('.chart-box-real-heart .chart-box-title').html("实时心率<span>（次/分）</span>");

            //海拔
            $('.chart-box-altitude .chart-box-title').html("<div class='chart-box-title'>海拔 <span>（米）</span> <span class='chart-box-bottom'></span></div>")

        } else if (language === 'en') {
            time.text('Time');
            kilometre.text('Distance');
            pace.text('Pace');
            $('.pace-normal-content').eq(0).html("<span>0</span>Calories");
            $('.pace-normal-content').eq(1).html("<span>0</span>Heart rate");

            //每公里配速
            var heart_href = $('.chart-box-pace .chart-box-title').attr('href') + '?language=' + $.getParam('language');
            $('.chart-box-pace .chart-box-title').attr('href', heart_href);


            $('.chart-box-pace .chart-box-title').html('Pace' + "<span>(min/km)</span>" + "<a href=" + heart_href + "><img src='/images/arrow@2x.png' alt='arrow'></a>");
            $('.chart-box-pace .chart-box-title').click(function (e) {
                e.stopPropagation();
                window.location.href = heart_href;
            });

            //实时配速
            $('.chart-box-real-pace .chart-box-title').html("Realtime pace<span>(min/km)</span>");

            //心率无区间
            $('.chart-box-heart .chart-box-title').html("Heart rate zone<a href=" + heartFive + "><img src='/images/ic_info_black_normal@2x.png' style='position: relative;width: 0.48rem;height: 0.48rem;vertical-align: middle;top: -0.04rem;left: 0.2rem'></a>")

            //实时心率
            $('.chart-box-real-heart .chart-box-title').html("Realtime heart rate<span>(beat/min)</span>");

            //海拔
            $('.chart-box-altitude .chart-box-title').html("<div class='chart-box-title'>Altitude <span>(m)</span> <span class='chart-box-bottom'></span></div>")
        } else if (language === 'zh_tw' || language === 'zh_hk') {
            time.text('時長');
            kilometre.text('公裏');
            pace.text('配速');
            $('.pace-normal-content').eq(0).html("<span>0</span>消耗");
            $('.pace-normal-content').eq(1).html("<span>0</span>平均心率");

            //每公里配速
            var heart_href = $('.chart-box-pace .chart-box-title').attr('href') + '?language=' + $.getParam('language');
            $('.chart-box-pace .chart-box-title').attr('href', heart_href);

            $('.chart-box-pace .chart-box-title').html('每公裏配速' + "<span>(分鍾/公裏)</span>" + "<a href=" + heart_href + "><img src='/images/arrow@2x.png' alt='arrow'></a>");
            $('.chart-box-pace .chart-box-title').click(function (e) {
                e.stopPropagation();
                window.location.href = heart_href;
            });


            //实时配速
            $('.chart-box-real-pace .chart-box-title').html("實時配速<span>(分鍾/公裏)</span>");

            //心率无区间
            $('.chart-box-heart .chart-box-title').html("心率五區間占比<a href=" + heartFive + "><img src='/images/ic_info_black_normal@2x.png' style='position: relative;width: 0.48rem;height: 0.48rem;vertical-align: middle;top: -0.04rem;left: 0.2rem'></a>")

            //实时心率
            $('.chart-box-real-heart .chart-box-title').html("實時心率<span>(次/分)</span>");

            //海拔
            $('.chart-box-altitude .chart-box-title').html("<div class='chart-box-title'>海拔 <span>(米)</span> <span class='chart-box-bottom'></span></div>")
        }
    }

    /**
     * 本次跑步的运动轨迹
     * @param dataObj
     */
    function CallBackSetRunningPoint(string) {

        var dataObj = JSON.parse(string);

        console.log("DataObj", dataObj);

        var partGroup = [];

        for (var i = 0; i < dataObj.length; i++) {
            partGroup.push(new AMap.LngLat(dataObj[i].longitude, dataObj[i].latitude));
        }

        //缩放屏幕比例至轨迹可完全显示在地图中
        myMapOption.myRoutes.setPath(partGroup);
        myMapOption.myMap.setFitView(myMapOption.myRoutes);

        //这时候开始截取第一张图：
        myMapOption.myMap.on('complete', function () {

            myMapOption.myRoutes.setOptions({
                strokeOpacity: 1,                       //轨迹透明度：
                strokeColor: '#00BB00 '                 //轨迹颜色：
            });

            myMapOption.myStartPoint.setIcon(
                new AMap.Icon({
                    size: new AMap.Size(20, 20),
                    imageSize: new AMap.Size(20, 20),
                    image: '/images/start@2x.png',
                    imageOffset: new AMap.Pixel(0, 0)
                })
            );

            var partLength = partGroup.length;
            myMapOption.myStartPoint.setPosition(partGroup[0]);
            myMapOption.myEndPoint.setPosition(partGroup[partLength - 1]);

            console.log(partLength);
        });
    }

    /**
     * 隐藏地图logo信息
     */
    function hideMapCopy() {
        $('.amap-logo').attr('href', 'javascript:void(0)').css({
            '-webkit-tap-highlight-color': 'transparent'
        });
        $('.amap-copyright').text('');
    }

    /**
     * 生成页面信息
     * @param string
     */
    function CallBackRunningData(string, type) {

        if (type == undefined) {
            console.log('CallBackRunningData()' + '  type is undefined');
        }
        var data = JSON.parse(string);

        setBodyData(data, type);

        $('.loading-box').hide();
    }

    /**
     * 生成页面基本信息
     * @param obj
     */
    function setBodyData(obj, type) {

        myMapOption.myPageType = type;

        var language = $.getParam('language');

        if (type == undefined) {
            console.log("setBodyData()" + "  type is undefined");
        }

        if (type == 1) {
            //初始化地图
            myMapOption.myMap = new AMap.Map('map-box', {
                zoom: 15,
                animateEnable: false,
                resizeEnable: true,
                dragEnable: false,
                mapStyle: 'normal',
                center: new AMap.LngLat(113.935364, 22.530001)
            });

            //初始化开始点：
            myMapOption.myStartPoint = new AMap.Marker({
                map: myMapOption.myMap,
                position: new AMap.LngLat(113.935364, 22.530001),
                zIndex: 300,                                    //设置角色层级，防止礼物压在了角色上
                icon: new AMap.Icon({
                    size: new AMap.Size(20, 20),                //图标大小
                    imageSize: new AMap.Size(20, 20),           //图像大小
                    image: "/images/start@2x.png",
                    imageOffset: new AMap.Pixel(0, 0)
                }),
                offset: new AMap.Pixel(-10, -10)
            });

            //初始化结束点：
            myMapOption.myEndPoint = new AMap.Marker({
                map: myMapOption.myMap,
                position: new AMap.LngLat(113.935364, 22.530001),
                zIndex: 300,
                icon: new AMap.Icon({
                    size: new AMap.Size(20, 20),
                    imageSize: new AMap.Size(20, 20),
                    image: "/images/end@2x.png",
                    imageOffset: new AMap.Pixel(0, 0)
                }),
                offset: new AMap.Pixel(-10, -10)
            });

            //初始化轨迹容器
            myMapOption.myRoutes = new AMap.Polyline({
                map: myMapOption.myMap,
                path: [],                        //设置线覆盖物路径
                strokeColor: "#3366FF",          //线颜色
                strokeOpacity: 0,                //线透明度
                strokeWeight: 6,                 //线宽
                strokeStyle: "solid",            //线样式
                isOutline: false,               //是否有轮廓线
                outlineColor: '#ffffff',         //轮廓线颜色
                lineJoin: 'round',
                zIndex: 50
            });

            hideMapCopy();
        } else if (type == 2) {
            //室内健身
            $('.map-box-bg').css({
                background: "url(/images/test/test_running_2.png) no-repeat center",
                backgroundSize: 'cover'
            });
            $('.map-box').css({'height': 'auto'});
            $('.chart-box-altitude').hide();
            $('.amap-maps').hide();

        } else if (type == 3) {
            //无网络
            $('.map-box-bg').css({
                background: "url(/images/test/test_running_3.png) no-repeat center",
                backgroundSize: 'cover'
            });
            $('.map-box').css({'height': 'auto'});
            $('.amap-maps').hide();
        }

        window.RUNNINGTIME = obj.time;

        $('.content-info-time i').text(obj.time);

        //生成配速
        var pace = changeSpeed(obj.pace);
        $('.content-info-pace i').text(pace);
        $('.content-info-kilometre i').text(obj.kilometre);
        var box = $('.pace-data-box .pace-data-normal .pace-normal-content span');

        if (language === 'zh_cn') {
            box.eq(0).text(obj.consume + ' 千卡');
            box.eq(1).text(obj.average + ' 次/分');
        } else if (language === 'en') {
            box.eq(0).text(obj.consume + ' Kcal');
            box.eq(1).text(obj.average + ' BPM');
        } else if (language === 'zh_tw' || language === 'zh_hk') {
            box.eq(0).text(obj.consume + ' 千卡');
            box.eq(1).text(obj.average + ' 次/分');
        }

        box.eq(2).text(obj.recovery);

        //累计爬高
        if (obj.altitude !== undefined && obj.altitude !== '') {
            console.log('obj.altitude=================', obj.altitude);
            if (language === 'zh_cn') {
                $('.chart-box-bottom').text('累计爬升 +' + obj.altitude);
            } else if (language === 'en') {
                $('.chart-box-bottom').text('Cumulative climb +' + obj.altitude);
            } else if (language === 'zh_tw' || language === 'zh_hk') {
                $('.chart-box-bottom').text('累計爬升 +' + obj.altitude);
            }
        } else {
            $('.chart-box-bottom').hide();
        }

        //通知客户端可以开始传输GPS数据：
        var userAgent = MygetUserAgentFun();

        try {
            if (userAgent == 'ios') {
                window.webkit.messageHandlers.PaceHistoryForDm.postMessage({method: 'WebMapIsReady'});
            } else {
                PaceHistoryForDm.WebMapIsReady();
            }
            console.log('地图初始化完成 - 通知客户端成功');
        } catch (e) {
            console.log("地图初始化完成，通知客户端 - 失败  PaceHistoryForDm.WebMapIsReady()");
        }
    }

    /**
     * 每公里配速数据
     * @param string
     * @constructor
     */
    function CallBackKilometreBarChart(string) {
        $('.chart-box-pace').show();
        var data = JSON.parse(string);
        if (data.length >= 1 && data[0] !== 0) {
            chartFrom.kilometreBarChart(data);
        } else {
            $('.chart-box-pace').hide();
        }
    }

    /**
     * 实时配速
     * @param string
     * @constructor
     */
    function CallBackKilometreLineChart(string) {
        $('.chart-box-real-pace').show();
        var data = JSON.parse(string);
        chartFrom.kilometreLineChart(data);
    }

    /**
     * 心率五区
     * @param string
     * @constructor
     */
    function CallBackHeartBarChart(string) {
        $('.chart-box-heart').show();
        var data = JSON.parse(string);
        chartFrom.heartBarChart(data);
    }

    /**
     * 实时心率
     * @param string
     * @constructor
     */
    function CallBackHeartLineChart(string) {
        $('.chart-box-real-heart').show();
        var data = JSON.parse(string);
        chartFrom.heartLineChart(data);
    }

    /**
     * 步频
     * @param string
     * @constructor
     */
    function CallBackPaceLineChart(string) {
        var data = JSON.parse(string);
        chartFrom.paceLineChart(data);
    }

    /**
     * 海拔
     * @param string
     * @constructor
     */
    function CallBackAltitudeLineChart(string) {
        $('.chart-box-altitude').show();
        var data = JSON.parse(string);
        chartFrom.altitudeLineChart(data);
    }

    //配速转换
    function changeSpeed(speed) {
        //先判断传过来的配速是否为小数(为兼容ios,ios暂未修改)
        if (speed.toString().indexOf('.') > 0) {
            speed = speed.toString().replace('.', "'") + "''";
            return speed;
        } else {
            if (speed !== 0) {
                var minute = Math.floor(speed / 60);

                var second = speed % 60;

                return minute + "'" + second + "''";
            } else {
                return "0'00''"
            }
        }
    }

    //生成截图
    function CallbackGetImages() {
        html2canvas($('#map-box')).then(function (canvas) {
            var img = convertCanvasToImage(canvas);
            var images = img.src;

            try {
                PaceHistoryForDm.NavShareImg(images)
            } catch (e) {
                console.log('base64传输失败')
            }
        });
    }

    /**
     * 隐藏全部的图表部分，在客户端调用时显示
     */
    function hideCharts() {
        $('.chart-box-pace').hide();
        $('.chart-box-real-pace').hide();
        $('.chart-box-heart').hide();
        $('.chart-box-real-heart').hide();
        $('.chart-box-altitude').hide();
    }

    /**
     * 中英文语言切换
     */
    function changeLangReach() {
        //
        $('.add-new-map-content')
    }

    window.CallBackRunningData = CallBackRunningData;
    window.CallBackSetRunningPoint = CallBackSetRunningPoint;

    //图表部分方法
    window.CallBackKilometreBarChart = CallBackKilometreBarChart;
    window.CallBackKilometreLineChart = CallBackKilometreLineChart;
    window.CallBackHeartBarChart = CallBackHeartBarChart;
    window.CallBackHeartLineChart = CallBackHeartLineChart;
    window.CallBackPaceLineChart = CallBackPaceLineChart;
    window.CallBackAltitudeLineChart = CallBackAltitudeLineChart;

    //生成截图并传给客户端
    window.CallbackGetImages = CallbackGetImages;


})(document, window, $);


